@charset "UTF-8";
html, body {
  width: 100%;
  height: 100%;
}

body {
  background-color: $brand-primary;
}

.login-wrap {
  .logo {
    padding-top: 80px;
    background-color: #fff;
    text-align: center;
  }
}

.form-login {
  position: relative;
  left: -50px;
  width: 330px;
  margin: 0 auto;
  padding-top: 60px;
  padding-bottom: 60px;
  .form-group {
    margin-bottom: 20px;
    @include clearfix();
  }
  label {
    float: left;
    width: 80px;
    padding-right: 15px;
    line-height: 30px;
    text-align: right;
    color: #fff;
  }
  input {
    float: left;
    width: 250px;
    height: 34px;
    line-height: 34px;
    border: 1px solid #fff;
    border-radius: 2px;
  }
  .btn-submit {
    width: 250px;
    height: 34px;
    margin-top: 20px;
    margin-left: 80px;
    line-height: 34px;
    font-size: 14px;
    background-color: #fff;
    border-radius: 20px;
    border: none;
  }
}