@charset "UTF-8";
// 变量
$gray-base: #000 !default;
$gray-darker: #333 !default; // #222
$gray-dark: #666 !default; // #333
$gray: #999 !default;
$gray-light: #ccc !default;
$gray-lighter: #f0f0f0 !default;

$brand-primary: #B91D2C !default;
$brand-success: #66cc33 !default;
$brand-info: #2196f3 !default;
$brand-warning: #ffff33 !default;
$brand-danger: #B91D2C !default;

$body-bg: #fff !default;

// 颜色
.body-bg {
  background-color: #fff;
}

.text-gray {
  color: $gray !important;
}

.text-gray-light {
  color: $gray-light !important;
}

.text-primary {
  color: $brand-primary !important;
}

.text-success {
  color: $brand-success !important;
}

.text-danger {
  color: $brand-danger !important;
}
